import React, { Component } from 'react';
import Tabs from "./component/Tabitem"; 
import "./App.css";

class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      planetList: [],
    }
  }
  componentDidMount() {
    fetch("https://assignment-machstatz.herokuapp.com/planet").then(res=>res.json()).then(
      result=>{
        this.setState({planetList: result})
      }
    )
  }
  handleClick = () => {
    console.log('clicked')
}
  render(){
    return (
      <div>

      <div>
        <h1>Planets List</h1>
       <Tabs> 
       
         <div label="plenets"> 
           {
             this.state.planetList.map((item) => {
               return (
                <li key={item.id}>
                  <div>
                    <span>{item.name}</span>
                       <button
                       style = {{marginLeft : 20}}
                       onClick = {() => {
                        const index = this.state.planetList.findIndex(x => x.id === item.id)
                        var favList = this.state.planetList
                        favList[index].isFavourite = !favList[index].isFavourite
                        this.setState({
                          planetList : favList,
                        })
                       }
                       }
                       >
                         {
                           item.isFavourite ? "UnFavorite" : "Fovrite"
                         }
                       </button>
                  </div>
              </li>
               )
             })
           }
         
         </div> 
         <div label="selected"> 
         {
             this.state.planetList.filter(item => item.isFavourite === true).map((item) => {
               return (
                <li key={item.id}>
                  <div>
                    {item.name}
                  </div>
              </li>
               )
             })
           }
         </div> 
          
       </Tabs> 
      </div>
      </div>
    );
  }
  
}

export default App;